import React from 'react';
import { Route, Switch, BrowserRouter as Router } from 'react-router-dom';
import HomePage from '../pages/home';
import LoginPage from '../pages/login';
import SignUpPage from '../pages/signup';
import Mypage from '../pages/mypage';
import SyllabusPage from '../pages/syllabus';
import EnrolmentPage from '../pages/enrolment';
import DefaultLayout from '../components/layout/DefaultLayout';
import MaterialPage from '../pages/material';
import ChatTestPage from '../pages/chat';
import { StudentDetailPage } from '../pages';
import CreateLecturePage from '../pages/lecture';
import StreamPage from '../pages/stream';
// import AuthLayout from '../components/layout/AuthLayout';
import MenuBarLayout from '../components/layout/MenuBarLayout';
import StreamLayout from '../components/layout/StreamLayout';
import StreamStudentPage from '../pages/student/stream';
// import RouteIf from '../RouteIf';
// const myRole = {
//     homePage: ROLE.NONE, // 사용자 관리 화면

//   }
const index = () => {
  return (
    <Router>
      <Switch>
        <Route exact path="/login" component={LoginPage} />
        <Route path="/signup" component={SignUpPage} />
        <DefaultLayout exact path="/" component={HomePage} />
        <DefaultLayout path="/home" component={HomePage} />
        <DefaultLayout path="/mypage" component={Mypage} />
        <StreamLayout path="/stream" component={StreamPage} />
        <MenuBarLayout path="/syllabus" component={SyllabusPage} />
        <MenuBarLayout path="/enrolment" component={EnrolmentPage} />
        <MenuBarLayout path="/studentdetail" component={StudentDetailPage} />
        <MenuBarLayout path="/material" component={MaterialPage} />
        <MenuBarLayout path="/lecture" component={CreateLecturePage} />
        <MenuBarLayout path="/chat" component={ChatTestPage} />
        <StreamLayout path="/streamstudent" component={StreamStudentPage} />
      </Switch>
    </Router>
  );
};

export default index;
