import { restApi } from '../lib/client';

export const postJoin = async ({ email, veri_code, name, password }) => {
  try {
    console.log(email, '데이터');
    const { data } = await restApi.post('/api/st/join', {
      email,
      veri_code,
      name,
      password,
    });
    if (data && data.code === 'api_203') {
      return true;
    }
    return false;
  } catch (error) {
    console.log('에러', error);
    throw error;
  }
};

export default { postJoin };
