import { restApi } from '../lib/client';

export const login = async ({ email, password }) => {
  try {
    // console.log(email, '데이터', response);
    const { data, cookies } = await restApi.post(
      '/api/signin',
      {
        email,
        password,
      },
      { withCredentials: true },
    );

    console.log(cookies, '헤더헤더');
    if (data && data.code === 'api_204') {
      return data;
    }
    return false;
  } catch (error) {
    throw error;
  }
};
