// style set file
import { createGlobalStyle } from 'styled-components';
import reset from 'styled-reset';
import GmarketLightOtf from '../assets/fonts/GmarketSansTTFLight.otf';
import GmarketLightWoff from './fonts/GmarketSansTTFLight.woff';
import GmarketLightWoff2 from './fonts/GmarketSansTTFLight.woff2';
import GmarketMediumOtf from './fonts/GmarketSansTTFMedium.otf';
import GmarketMediumWoff from './fonts/GmarketSansTTFMedium.woff';
import GmarketMediumWoff2 from './fonts/GmarketSansTTFMedium.woff2';
import GmarketBoldOtf from './fonts/GmarketSansTTFBold.otf';
import GmarketBoldWoff from './fonts/GmarketSansTTFBold.woff';
import GmarketBoldWoff2 from './fonts/GmarketSansTTFBold.woff2';

const GlobalStyle = createGlobalStyle`
  /* Light */
  @font-face {
  font-family: 'Gmarket Light';
  font-weight: 300;
  font-style: normal;
  src: url(${GmarketLightOtf}) format('truetype'),
    url(${GmarketLightWoff}) format('woff'),
    url(${GmarketLightWoff2}) format('woff2');
  }
  /* Medium */
  @font-face {
    font-family: 'Gmarket Medium';
    font-weight: 500;
    font-style: normal;
    src: url(${GmarketMediumOtf}) format('truetype'),
    url(${GmarketMediumWoff}) format('woff'),
    url(${GmarketMediumWoff2}) format('woff2');
  }
  /* Bold */
  @font-face {
    font-family: 'Gmarket Bold';
    font-weight: 700;
    font-style: normal;
    src: url(${GmarketBoldOtf}) format('truetype'),
      url(${GmarketBoldWoff}) format('woff'),
      url(${GmarketBoldWoff2}) format('woff2');
  }
  ${reset};
  * {
      box-sizing: border-box;
     
  }
  
  html, body, div, span, applet, object, iframe,
  h1, h2, h3, h4, h5, h6, p, blockquote, pre,
  a, abbr, acronym, address, big, cite, code,
  del, dfn, em, img, ins, kbd, q, s, samp,
  small, strike, sub, sup, tt, var,
  u, i, center, dl, dt, dd, ol, ul, li,
  fieldset, form, label, legend,
  table, caption, tbody, tfoot, thead, tr, th, td,
  article, aside, canvas, details, embed,
  figure, figcaption, footer, header, hgroup,
  menu, nav, output, ruby, section, summary,
  time, mark, audio, video , button, input,
  select, textarea {
    font-family: 'Gmarket Light';
    word-break: break-all;
  }
  a {
      color: inherit;
      text-decoration: none;
  }
  input,
  button {
      &:focus,
      &:active{ outline: none }
  }
  body {
    background-color :${({ theme }) => theme.colors.veryLightBlue};
    height :100%;
  }
 html {
   height :100%;
 }
`;

export default GlobalStyle;
