import { SEND_CHAT, RECEIVE_CHAT, CLEAR_CHAT } from './types';

export const sendChat = () => {
  return {
    type: SEND_CHAT,
  };
};

export const receiveChat = (payload) => {
  return {
    type: RECEIVE_CHAT,
    payload,
  };
};

export const clearChat = () => {
  return {
    type: CLEAR_CHAT,
  };
};
