import React from 'react';
import styled from 'styled-components';
const ChatModal = ({
  visible,
  children,
  customCloseable,
  closeable,
  onClose,
  label,
  width,
  height,
  font,
  image,
  padding,
  top,
  right,
  left,
  labelContent,
}) => {
  const handleClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose(e);
    }
  };
  const handleClose = (e) => {
    if (onClose) {
      onClose(e);
    }
  };
  return (
    <ModalWrapper onClick={customCloseable ? handleClick : null} tabIndex="-1" visible={visible}>
      <ModalInner width={width} height={height} padding={padding} top={top} right={right} left={left}>
        <ModalHeader>
          <ModalDetailDiv>
            <TitleImg src={image} mr="16px" />
            <InnerDiv>
              <ModalTitle font={font}>{label}</ModalTitle>
              <ModalContent>{labelContent}</ModalContent>
            </InnerDiv>
          </ModalDetailDiv>
          {closeable && (
            <img
              src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/close.svg'}`}
              onClick={handleClose}
              alt="close"
            />
          )}
        </ModalHeader>
        <ModalLine>
          <Line></Line>
        </ModalLine>
        {children}
        <ModalInput placeholder="메세지를 입력해주세요." />
      </ModalInner>
    </ModalWrapper>
  );
};

const ModalWrapper = styled.div`
  box-sizing: border-box;
  display: ${(props) => (props.visible ? 'block' : 'none')};
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  overflow: auto;
  outline: 0;
`;

const ModalInner = styled.div`
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  margin: 38px 120px 4px 190px;
  box-sizing: border-box;
  position: relative;
  background-color: ${({ theme }) => theme.colors.paleGreyTwo};
  /* top: 50%; */
  top: ${(props) => props.top};
  right: ${(props) => props.right};
  left: ${(props) => props.left};
  transform: translateY(-50%);
  margin: 0 auto;
  padding: ${(props) => props.padding};
  border-radius: 4px;
  /* box-shadow: 5px 10px 15px 0 rgba(0, 0, 0, 0.16); */
`;

const ModalHeader = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 5px;
`;
const ModalDetailDiv = styled.div`
  flex: 1;
  display: flex;
  margin-left: 15px;
`;
const TitleImg = styled.img`
  margin-right: ${(props) => props.mr};
`;

const InnerDiv = styled.div``;
const ModalTitle = styled.div`
  display: flex;
  flex: 1;
  font-size: ${(props) => props.font};
  font-family: 'Gmarket Bold';
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: ${({ theme }) => theme.colors.black};
`;

const ModalContent = styled.div`
  margin-top: 3px;
`;
const ModalLine = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
`;

const Line = styled.div`
  width: 28px;
  height: 2px;
  background-color: ${({ theme }) => theme.colors.veryLightPink};
`;

const ModalInput = styled.textarea`
  width: 100%;
  height: 56px;
  position: fixed;
  bottom: 0%;
  left: 0%;
  padding-left: 17px;
  padding-top: 19px;
  outline: 0;
  border: 0;
  background-color: ${({ theme }) => theme.colors.whiteOp5};
`;
export default ChatModal;
