import React from 'react';
import styled from 'styled-components';
const Modal = ({
  visible,
  children,
  customCloseable,
  closeable,
  onClose,
  label,
  width,
  height,
  font,
  image,
  padding,
  top,
  right,
  left,
  mb,
  sideWidth,
  sideHeight,
  sidePadding,
  sideTop,
  sideRight,
  sideLeft,
  sideChildren,
  bc,
}) => {
  const handleClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose(e);
    }
  };
  const handleClose = (e) => {
    if (onClose) {
      onClose(e);
    }
  };
  return (
    <ModalOverlay>
      <ModalWrapper onClick={customCloseable ? handleClick : null} tabIndex="-1" visible={visible}>
        <ModalInner
          position="relative"
          width={width}
          height={height}
          padding={padding}
          top={top}
          right={right}
          left={left}
          bc={bc}
        >
          <ModalHeader>
            <TitleImg src={image} mr="7px" />
            <ModalTitle font={font}>{label}</ModalTitle>
            {closeable && (
              <img src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/close.svg'}`} onClick={handleClose} />
            )}
          </ModalHeader>
          <ModalLine mb={mb}>
            <Line></Line>
          </ModalLine>
          {children}
        </ModalInner>

        <ModalInner
          position="absolute"
          width={sideWidth}
          height={sideHeight}
          padding={sidePadding}
          top={sideTop}
          right={sideRight}
          left={sideLeft}
        >
          {sideChildren}
        </ModalInner>
      </ModalWrapper>
    </ModalOverlay>
  );
};
const ModalOverlay = styled.div`
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: ${({ theme }) => theme.colors.blueGrey};
`;
const ModalWrapper = styled.div`
  box-sizing: border-box;
  display: ${(props) => (props.visible ? 'block' : 'none')};
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  overflow: auto;
  outline: 0;
`;

const ModalInner = styled.div`
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  margin: 38px 120px 4px 190px;
  box-sizing: border-box;
  position: ${(props) => props.position};
  /* background-color: #fff; */
  background-color: ${(props) => props.bc};
  /* top: 50%; */
  top: ${(props) => props.top};
  right: ${(props) => props.right};
  left: ${(props) => props.left};
  transform: translateY(-50%);
  margin: 0 auto;
  padding: ${(props) => props.padding};
  border-radius: 4px;
  box-shadow: 5px 10px 15px 0 rgba(0, 0, 0, 0.16);
`;

const ModalHeader = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 5px;
`;
const TitleImg = styled.img`
  margin-right: ${(props) => props.mr};
`;
const ModalTitle = styled.div`
  display: flex;
  flex: 1;
  font-size: ${(props) => props.font};
  font-family: 'Gmarket Bold';
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: ${({ theme }) => theme.colors.black};
`;
const ModalLine = styled.div`
  margin-bottom: ${(props) => props.mb};
  display: flex;
  justify-content: center;
`;

const Line = styled.div`
  width: 28px;
  height: 2px;
  background-color: ${({ theme }) => theme.colors.veryLightPink};
`;
export default Modal;
