import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'react-redux';
const Notice = ({
  titleImage,
  title,
  plusImg,
  width,
  height,
  padding,
  font,
  children,
  mr,
  mb,
  overflow,
  ml,
  hContent,
  mbTitle,
  mtWrapper,
  onPlus,
}) => {
  const { userInfo } = useSelector((state) => state.auth);
  return (
    <NoticeWrapper width={width} height={height} padding={padding} mr={mr} mtWrapper={mtWrapper}>
      <NoticeTitleDiv mbTitle={mbTitle}>
        <TitleImg src={titleImage} />
        <TitleName font={font}>{title}</TitleName>
        {userInfo?.part === 'student' ? '' : <PlusImg src={plusImg} onClick={onPlus} />}
      </NoticeTitleDiv>
      <LineDiv mb={mb}>
        <Line />
      </LineDiv>
      <NoticeCotentDiv overflow={overflow} ml={ml} hContent={hContent}>
        {children}
      </NoticeCotentDiv>
    </NoticeWrapper>
  );
};

const NoticeWrapper = styled.div`
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  border-radius: 10px;
  box-shadow: 4px 4px 24px 0 rgba(0, 0, 0, 0.08);
  background-color: ${({ theme }) => theme.colors.white};
  padding: ${(props) => props.padding};
  margin-right: ${(props) => props.mr};
  margin-top: ${(props) => props.mtWrapper};
`;
const NoticeTitleDiv = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: ${(props) => props.mbTitle};
`;

const TitleImg = styled.img`
  margin-right: 16px;
`;
const TitleName = styled.div`
  font-family: 'Gmarket bold';
  font-size: ${(props) => props.font};
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: ${({ theme }) => theme.colors.black};
  margin-right: 9px;
`;

const PlusImg = styled.img``;
const LineDiv = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: ${(props) => props.mb};
`;

const Line = styled.div`
  width: 28px;
  height: 2px;
  background-color: ${({ theme }) => theme.colors.veryLightPink};
`;
const NoticeCotentDiv = styled.div`
  /* overflow: scroll; */
  overflow: ${(props) => props.overflow};
  height: ${(props) => props.hContent};
  margin-left: ${(props) => props.ml};
`;

export default Notice;
