import React, { useState } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const Header = () => {
  const [isProfiled, setIsProfiled] = useState(false);
  // const dispatch = useDispatch();
  const handleProfile = () => {
    setIsProfiled(!isProfiled);
  };
  const mypageClick = async () => {
    setIsProfiled(false);
  };
  return (
    <Container>
      <RowDiv>
        <LogoDiv>
          <Logo src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/home-logo.svg'}`} />
        </LogoDiv>
        <UserIconDiv>
          <UserIcon
            src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/header-profile.svg'}`}
            onClick={handleProfile}
          />
          {isProfiled ? (
            <ProfileDiv>
              <Link to="/mypage">
                <MyPageDiv onClick={mypageClick}>마이 페이지</MyPageDiv>
              </Link>
              <Link to="/login">
                <LogoutDiv>로그아웃</LogoutDiv>
              </Link>
            </ProfileDiv>
          ) : null}
          <UserName>정민경 </UserName>
          <UserText> &nbsp;님 안녕하세요</UserText>
        </UserIconDiv>
      </RowDiv>
    </Container>
  );
};

const Container = styled.div`
  background-color: ${({ theme }) => theme.colors.whiteOp8};
  height: 54px;
  box-shadow: 4px 4px 24px 0 rgba(0, 0, 0, 0.08);
`;

const RowDiv = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
`;
const LogoDiv = styled.div`
  flex: 1;
  align-items: center;
  margin-top: 8px;
  margin-bottom: 9px;
  margin-left: 40px;
`;
const Logo = styled.img`
  justify-content: flex-end;
`;
const UserIconDiv = styled.div`
  display: flex;
  margin-right: 40px;
  align-items: center;
  margin-top: 11px;
  margin-bottom: 8px;
  position: relative;
`;
const UserIcon = styled.img`
  margin-right: 9px;
`;

const ProfileDiv = styled.div`
  position: absolute;
  right: 121px;
  top: 30px;
  width: 128px;
  height: 72px;
  border-radius: 4px;
  box-shadow: 4px 4px 24px 0 rgba(0, 0, 0, 0.08);
  background-color: ${({ theme }) => theme.colors.white};
`;

const MyPageDiv = styled.div`
  font-family: 'Gmarket Medium';
  font-size: 13px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 44px;
  letter-spacing: normal;
  text-align: center;
`;

const LogoutDiv = styled.div`
  font-family: 'Gmarket Medium';
  font-size: 13px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
`;
const UserText = styled.div`
  font-family: 'Gmarket Light';
  font-size: 10px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: ${({ theme }) => theme.colors.black};
`;

const UserName = styled.div`
  font-family: 'Gmarket Bold';
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: ${({ theme }) => theme.colors.black};
`;
export default Header;
