import React from 'react';
import styled from 'styled-components';

const Sidebar = ({ width, height, children }) => {
  const [xPosition, setX] = React.useState(-width);

  //   React.useEffect(() => {
  //     setWidth(0);
  //   });
  const toggleMenu = () => {
    if (xPosition < 0) {
      setX(0);
    } else {
      setX(-width);
    }
  };
  return (
    <SideBarDiv style={{ transform: `translatex(${xPosition}px)`, width: width, minHeight: height }}>
      <ToggleMenuDiv onClick={() => toggleMenu()} style={{ transform: `translate(${width}px, 4vh)` }}>
        <MenuArrowImg src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/menu-arrow.svg'}`} />
      </ToggleMenuDiv>
      <div>{children}</div>
    </SideBarDiv>
  );
};

const SideBarDiv = styled.div`
  height: 100% !important;
  display: flex;
  flex-direction: column;

  border-radius: 4px;
  box-shadow: 4px 4px 24px 0 rgba(0, 0, 0, 0.08);
  background-color: ${({ theme }) => theme.colors.whiteOp5};
  transition: 0.8s ease;
  /* z-index: -1; */
`;

const ToggleMenuDiv = styled.div`
  border-top-right-radius: 10rem;
  border-bottom-right-radius: 9rem;
  position: absolute;
  outline: none;
  /* z-index: 1; */
`;
const MenuArrowImg = styled.img``;
export default Sidebar;
