import { combineReducers, createStore, applyMiddleware, compose } from 'redux';
import { authReducer } from './redux/reducers/auth';
import { chatReducer } from './redux/reducers/chat';
import { partReducer } from './redux/reducers/part';
import { mypageReducer } from './redux/reducers/mypage';

const rootReducer = combineReducers({
  auth: authReducer,
  chat: chatReducer,
  part: partReducer,
  mypage: mypageReducer,
});

export const store = createStore(
  rootReducer,
  compose(applyMiddleware(), window.__REDUX_DEVTOOLS_EXTENSION__ ? window.__REDUX_DEVTOOLS_EXTENSION__() : (f) => f),
);
console.log('스토어', store.getState());
