import { MYPAGE_REQUEST, MYPAGE_SUCCESS, MYPAGE_FAILURE } from './types';

export const mypageRequestAction = (payload) => ({
  type: MYPAGE_REQUEST,
  payload,
});

export const mypageSuccessAction = (payload) => ({
  type: MYPAGE_SUCCESS,
  payload,
});

export const mypageFailureAction = (payload) => ({
  type: MYPAGE_FAILURE,
  payload,
});
