import { MYPAGE_REQUEST, MYPAGE_SUCCESS, MYPAGE_FAILURE } from '../actions/types';
import produce from '../../lib/produce';

const initialState = {
  mypageLoading: false,
  mypageSuccess: false,
  mypageError: null,
  me: null,
};
export const mypageReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    {
      console.log('마이페이지 액션!!!', action);
    }
    switch (action.type) {
      case MYPAGE_REQUEST:
        draft.mypageLoading = true;
        draft.mypageError = null;
        draft.mypageSuccess = false;
        break;
      case MYPAGE_SUCCESS:
        draft.mypageLoading = false;
        draft.me = action.payload;
        draft.mypageSuccess = true;
        break;
      case MYPAGE_FAILURE:
        draft.mypageLoading = false;
        draft.mypageError = action.error;
        break;
      default:
        return state;
    }
  });
