import React, { useState, useCallback, useEffect, useContext } from 'react';
import styled, { ThemeContext } from 'styled-components';
import Modal from '../../components/common/modal/Modal';
import { Link } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { login, temp_password } from '../../api/login';
import useInput from '../../components/hooks/useInput';
import Cookies from 'js-cookie';
import { setToken } from '../../lib/client';
import { useDispatch, useSelector } from 'react-redux';
import { loginRequestAction, loginSuccessAction } from '../../redux/actions/auth';
const LoginPage = (props) => {
  const dispatch = useDispatch();
  const { logInLoading, logInError, userInfo } = useSelector((state) => state.auth);
  // console.log('값!!!!', userInfo.part, logInLoading);
  useEffect(() => {
    if (logInError) {
      alert(logInError);
    }
  }, [logInError]);
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();

  const [isModalVisible, setIsModalVisible] = useState(false);
  const [emailError, setEmailError] = useState(false);
  const [findEmail, onChangeFindEmail] = useInput('');
  const themeContext = useContext(ThemeContext);
  const openModal = () => {
    setIsModalVisible(true);
  };
  const closeModal = () => {
    setIsModalVisible(false);
  };
  const handleLogin = useCallback((e) => {
    setEmailError(true);
  });
  const onSubmit = async (values) => {
    console.log('벨류', values);

    try {
      await dispatch(loginRequestAction(values));
      console.log(loginRequestAction(values), '디스패치');
      const data = await login(values);

      if (data) {
        props.history.push('/');
        await dispatch(loginSuccessAction(data));
        console.log('데이터 파트', userInfo.part);
      } else {
        alert('다시 한번 입력해주세요');
      }
    } catch (err) {
      if (err.response?.status === 411) {
        alert('비밀번호를 틀렸습니다');
      }
    }
  };
  const onFindPw = async () => {
    try {
      const data = await temp_password({
        email: findEmail,
      });
      console.log('코드!', data);
      // await dispatch(signUp(values));
    } catch (err) {
      console.log(err, '서버에러');
      if (err.response.status === 411) {
        alert('비밀번호를 틀렸습니다');
      }
    }
  };
  return (
    <Container>
      <LoginForm onSubmit={handleSubmit(onSubmit)}>
        <CAUImg src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/logo.svg'}`} />
        <LoginTextDiv>
          <Text mb="8px">LOG-IN</Text>
          <Line />
        </LoginTextDiv>
        <InputDiv m="17px 800px 4px 801px" pwColor={emailError ? 'solid 2px red' : '0'}>
          <DefaultText mr="13px">이메일</DefaultText>
          <DefaultInput
            {...register('email', {
              required: true,
              pattern: {
                value: /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i,
                message: '이메일 형식으로 입력하세요',
              },
            })}
          />
        </InputDiv>
        {errors && <h1>{errors?.email?.message}</h1>}
        <ErrorDiv>{emailError && <ErrorMessage>!가입되지 않은 회원입니다.</ErrorMessage>}</ErrorDiv>
        <InputDiv m="0px 800px 4px 801px">
          <DefaultText>비밀번호</DefaultText>
          <DefaultInput
            type="password"
            {...register('password', {
              required: true,
              maxLength: { value: 30, message: '20자 까지 가능합니다' },
            })}
          />
        </InputDiv>
        {errors && <h1>{errors?.password?.message}</h1>}

        <ButtonDiv>
          <DefaultButton mr="48px" type="submit">
            <ButtonText>로그인</ButtonText>
          </DefaultButton>
          <DefaultButton>
            <Link to="/signup">
              <ButtonText>회원가입</ButtonText>
            </Link>
          </DefaultButton>
        </ButtonDiv>
        <FindModalA onClick={openModal}>
          <ins>비밀번호 찾기</ins>
        </FindModalA>
      </LoginForm>
      {isModalVisible && (
        <Modal
          visible={isModalVisible}
          closeable={true}
          customCloseable={true}
          onClose={closeModal}
          label={'비밀번호 찾기'}
          width={'468px'}
          height={'154px'}
          font={'16px'}
          padding={'12px 12px'}
          top={'50%'}
          bc={themeContext.colors.white}
        >
          {/* 꼭 다시 div 로 바꾸기  --> 현재 form */}
          <ModalContentDiv>
            <ModalContent>가입시 입력하셨던 이메일을 입력해주세요.</ModalContent>
            <ModalInputDiv>
              <ModalText>이메일</ModalText>
              <ModalInput value={findEmail} onChange={onChangeFindEmail} />
              <ModalButton onClick={onFindPw}>
                <ModalButtonText>전송</ModalButtonText>
              </ModalButton>
            </ModalInputDiv>
          </ModalContentDiv>
        </Modal>
      )}
    </Container>
  );
};

