import React, { useState, useRef } from 'react';
import styled from 'styled-components';
import DayTime from '../DayTime/DayTime';
const AccordionInput = ({ title, day, detailContent, newImage, polygonImg, wContent }) => {
  const [setActive, setActiveState] = useState('');
  const [setHeight, setHeightState] = useState('0px');

  const [question, setIsQuestion] = useState(false);
  const content = useRef(null);
  function toggleAccordion() {
    setActiveState(setActive === '' ? 'active' : '');
    setHeightState(setActive === 'active' ? '0px' : `${content.current.scrollHeight}px`);
  }
  return (
    <Wrapper>
      <InnerDiv className={`accordion ${setActive}`} onClick={toggleAccordion}>
        <TitleDiv>
          <Title>{title}</Title>
          <NewImg src={newImage} />
        </TitleDiv>
        {/* <DayImg src={DayImg}/>
        <Day>{day}</Day> */}
        {question ? (
          <DayTime
            calImg={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/small-calendar.svg'}`}
            calTitle={'2021.04.25'}
            timeImg={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/small-time.svg'}`}
            timeTitle={'PM :1:15'}
          />
        ) : (
          <Day>{day}</Day>
        )}
      </InnerDiv>
      <AccordionContent ref={content} style={{ maxHeight: `${setHeight}` }} wContent={wContent}>
        <TestDiv>
          <AccordDetail dangerouslySetInnerHTML={{ __html: detailContent }} onClick={toggleAccordion} />
        </TestDiv>
      </AccordionContent>
    </Wrapper>
  );
};

const Wrapper = styled.div``;

const InnerDiv = styled.div`
  display: flex;
  /* margin-bottom: 20px; */
  cursor: pointer;
  align-items: center;
  border: none;
  outline: none;
  transition: background-color 0.6s ease;

  &:hover {
    background-color: #eaedf2;
  }
`;
const Title = styled.div`
  font-family: 'Gmarket Medium';
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: ${({ theme }) => theme.colors.black};
  margin-right: 4px;
`;
const TitleDiv = styled.div`
  flex: 1;
  display: flex;
  justify-content: flex-start;
`;
const NewImg = styled.img``;
const Day = styled.div`
  font-family: 'Gmarket Light';
  font-size: 12px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: ${({ theme }) => theme.colors.black};
`;
const AccordionContent = styled.div`
  overflow: hidden;
  transition: max-height 0.6s ease;
  /* width: 525px; */
  width: ${(props) => props.wContent};
  height: 98px;
  border-radius: 4px;
  background-color: ${({ theme }) => theme.colors.whileOp4};
  margin-bottom: 14px;
  margin-top: 6px;
`;
const TestDiv = styled.div`
  display: flex;
  flex-direction: column;
`;

const AccordDetail = styled.div`
  padding: 14px 16px;
  overflow: scroll;
  /* height: 60px; */
  /* height: 88px; */
  height: 98px;
  font-family: 'Gmarket Light';
  font-size: 12px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  background-image: url(${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/polygon.svg'});
  background-repeat: no-repeat;
  background-position: center 88px;
`;

export default AccordionInput;
