import React, { useCallback } from 'react';
import io from 'socket.io-client';
import { useDispatch, useSelector } from 'react-redux';
import { receiveChat } from '../../redux/actions/chat';
const ChatTestPage = () => {
  const socket = io.connect('http://localhost:3005');

  const chatData = useSelector((state) => state.chat);
  //store의 state의 데이터를 할당할 수 있도록 하는 function
  {
    console.log('뭘까?', chatData);
  }
  const dispatch = useDispatch();
  //redux store에 설정된 action에 대한 dispatch를 연결하는 hook

  const socketSubscribe = useCallback(() => {
    socket.on('보내기 채팅', ({ data }) => {
      console.log('보내보내', data);
      dispatch(receiveChat(data));
    });

    console.log('보낸다', dispatch(receiveChat()));
  });

  return (
    <div>
      <button onClick={socketSubscribe}>버튼</button>
    </div>
  );
};

export default ChatTestPage;
