import React, { useState } from 'react';
import styled from 'styled-components';
const RadioInput = () => {
  const [radio, setRadio] = useState();
  const handleClick = () => {
    setRadio((s) => !s);
  };
  return (
    <div onClick={handleClick}>
      {!radio ? (
        <Image src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/stream-border-circle.svg'}`} />
      ) : (
        <Image src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/stream-small-circle.svg'}`} />
      )}
    </div>
  );
};

const Image = styled.img``;
export default RadioInput;
