import { SEND_CHAT, RECEIVE_CHAT, CLEAR_CHAT } from '../actions/types';

const initialState = {
  chatList: [], //array
};

export const chatReducer = (state = initialState, action) => {
  switch (action.type) {
    case RECEIVE_CHAT: //보내는 채팅 액션값
      let newChatList = state.chatList.slice();
      newChatList.push(action.payload);
      return {
        ...state,
        chatList: newChatList,
      };
    case CLEAR_CHAT: // 지워주는 채팅 액션값
      return { ...state, chatList: [] };
    default:
      // RECEIVE_CHAT 아니고, CLEAR_CHAT 아니면 디폴트 값
      return state;
  }
};
