import React, { useContext } from 'react';
import styled, { ThemeContext } from 'styled-components';
const TwoButton = ({ yesLabel, noLabel, closeModal, yesBc, noBc, backColor }) => {
  const themeContext = useContext(ThemeContext);
  // themeContext.colors.prussianBlue
  // themeContext.colors.veryLightBlue
  return (
    <ModalDiv jc="center" backColor={backColor}>
      <ModalButton bc={yesBc} mr={'24px'}>
        <ButtonText onClick={closeModal} color={themeContext.colors.white}>
          {yesLabel}
        </ButtonText>
      </ModalButton>
      <ModalButton bc={noBc}>
        <ButtonText onClick={closeModal} color={themeContext.colors.prussianBlue}>
          {noLabel}
        </ButtonText>
      </ModalButton>
    </ModalDiv>
  );
};

const ModalDiv = styled.div`
  display: flex;
  margin-bottom: 19px;
  margin-top: 16px;
  justify-content: ${(props) => props.jc};
  align-items: ${(props) => props.ai};
`;

const ModalButton = styled.div`
  width: 60px;
  height: 29px;
  border-radius: 30px;
  background-color: ${(props) => props.bc};
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: ${(props) => props.mr};
`;

const ButtonText = styled.div`
  font-family: 'Gmarket bold';
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: ${(props) => props.color};
`;
export default TwoButton;
