import React, { useState } from 'react';
import styled from 'styled-components';

const CheckBoxInput = ({ label, checkList, onCheck, mt, idx }) => {
  console.log('뭔데!!!', label, checkList, onCheck);
  return (
    <CheckWrapper mt={mt}>
      <CheckInnerDiv>
        <CheckDiv onClick={() => onCheck(idx)}>
          {!checkList[idx] ? (
            <CheckImg src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/close-check.gif'}`} />
          ) : (
            <CheckImg src={`${process.env.REACT_APP_S3_BUCKETS_HOST + '/images/open-check.gif'}`} />
          )}
        </CheckDiv>

        <CheckText>{label}</CheckText>
      </CheckInnerDiv>
    </CheckWrapper>
  );
};

const CheckWrapper = styled.div`
  display: flex;
  align-items: center;
  margin-top: ${(props) => props.mt};
`;
const CheckInnerDiv = styled.div`
  display: flex;
  align-items: center;
`;
const CheckDiv = styled.div`
  display: flex;
  width: 10px;
  height: 10px;
  border-radius: 2px;
  border: solid 1px #707070;
  background-color: ${({ theme }) => theme.colors.white};
  margin-right: 6px;
`;
const CheckImg = styled.img``;
const CheckText = styled.div`
  font-family: 'Gmarket bold';
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: ${({ theme }) => theme.colors.black};
`;
export default CheckBoxInput;
