import { restApi } from '../lib/client';

export const postSmsAuth = async ({ email }) => {
  try {
    console.log(email, '데이터');
    const { data } = await restApi.post('/api/st/join/veri_code', {
      email,
    });
    if (data && data.code === 'api_201') {
      return true;
    }
    return false;
  } catch (error) {
    console.log('이메일에러', error.response);

    throw error;
  }
};

export const postSmsCheck = async ({ email, veri_code }) => {
  try {
    const { data } = await restApi.post('/api/st/join/confirm_code', {
      email,
      veri_code,
    });
    console.log('데이터', data);
    if (data && data.code === 'api_202') {
      return true;
    }
    return false;
  } catch (error) {
    console.log('에러!!', error);
    throw error;
  }
};

export default { postSmsAuth, postSmsCheck };
